import { Component, OnInit } from '@angular/core';
import {Video} from'./../video';
import {VideoService} from './../video.service';

@Component({
  selector: 'app-video-center',
  templateUrl: './video-center.component.html',
  styleUrls: ['./video-center.component.css']
})
export class VideoCenterComponent implements OnInit {

  private hideNewVideo:boolean = true;

  videos:Array<Video>
  /*
  videos:Video[]=[{'_id':'1','title':'1','url':'1','description':'1'},
  {'_id':'1','title':'1','url':'1','description':'1'},
  {'_id':'1','title':'1','url':'1','description':'1'}
  ];
  */
  selectedVideo: Video;
  onSelect(video:Video){
    this.hideNewVideo = true;
    this.selectedVideo = video;
  }

  constructor(private videoService:VideoService) { 
      console.log('here');
  }

  OnAddVideo(video:Video){
    this.videoService.postVideos(video).subscribe((resNewVideo)=>{
      this.videos.push(resNewVideo);
      this.selectedVideo = video;
      this.hideNewVideo = true;
    });
  }

  onDeleteVideoEvent(video:any){
    let videoArray = this.videos;
    this.videoService.deleteVideo(video).subscribe((resDeleteVideo)=>
    {
      for(let i=0;i<this.videos.length;i++)
       {
         if (this.videos[i]._id == video._id)
         this.videos.splice(i,1);
       }
    });
    this.selectedVideo = null;
  }

  onUpdateVideoEvent(video:any){
    this.videoService.putVideos(video).subscribe((resUpdateVideo)=>{
      video=resUpdateVideo;
      this.selectedVideo = null;
    });
  }

  ngOnInit() {
    this.videoService.getVideos()
    .subscribe(resVideoData=>this.videos=resVideoData)
  }
  onClickAdd(){
    this.hideNewVideo = false;
  }
}