import { Injectable } from '@angular/core';
import {Http,Response,Headers,RequestOptions, ResponseOptions} from '@angular/http';
import {HttpClient} from '@angular/common/http';
import {map} from 'rxjs/operators';
import { Video } from './video';
import { HttpResponse } from 'selenium-webdriver/http';


@Injectable({
  providedIn: 'root'
})
export class VideoService {
  private _getUrl = 'api/videos';
  private _postUrl = 'api/videos';
  private _putUrl = 'api/videos/';
  private _deleteUrl = 'api/videos/';

  constructor(private _http:Http) {
  }

  getVideos(){
    
    return this._http.
    get(this._getUrl).pipe(
    map((response:Response)=>response.json()));
  }

  putVideos(video:Video){
    let header = new Headers({'content-type':'application/json'});
    let options = new RequestOptions({headers:header});
    return this._http.put(this._putUrl+video._id,video,options).pipe(
      map((response:Response)=>response.json())
    );
  }

  postVideos(video:Video){
    let headers = new Headers({
      "content-type":"application/json"
    });
    let options = new RequestOptions({headers:headers});
    return this._http.post(this._postUrl,JSON.stringify(video),options).pipe(
      map((response:Response)=>response.json())
    );
  }
  deleteVideo(video:Video){
    return this._http.delete(this._deleteUrl+video._id).pipe(
      map((response:Response)=>response.json()));
  }
}
